<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-date"></i> </el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="content-title"></div>
        <div class="plugins-tips">
            Element UI
            <a href="http://element.eleme.io/#/zh-CN/component/upload" target="_blank">Element UI Upload</a>
        </div>
        <el-upload
            class="upload-demo"
            drag
            action="/api/posts/"
            multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
            <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
        </el-upload>
        <div class="content-title"></div>
        <img class="pre-img" :src="src" alt="">
        <vue-core-image-upload :class="['pure-button','pure-button-primary','js-btn-crop']"
                               :crop="true"
                               text=""
                               url="/api/posts/"
                               extensions="png,gif,jpeg,jpg"
                               @:imageuploaded="imageuploaded"
                               @:errorhandle="handleError"></vue-core-image-upload>
    </div>
</template>

<script>
  import VueCoreImageUpload from 'vue-core-image-upload'
  export default {
    data: function () {
      return {
        src: './static/img/img.jpg',
        fileList: []
      }
    },
    components: {
      VueCoreImageUpload
    },
    methods: {
      imageuploaded (res) {
        console.log(res)
      },
      handleError () {
        this.$notify.error({
          title: '',
          message: ''
        })
      }
    }
  }
</script>

<style scoped>
    .pure-button{
        width:150px;
        height:40px;
        line-height: 40px;
        text-align: center;
        color: #fff;
        border-radius: 3px;
        background: rgb(64, 158, 255);
    }
    .g-core-image-corp-container .info-aside{
        height:45px;
    }
    .el-upload--text {
        background-color: #fff;
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        box-sizing: border-box;
        width: 360px;
        height: 180px;
        text-align: center;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .el-upload--text .el-icon-upload {
        font-size: 67px;
        color: #97a8be;
        margin: 40px 0 16px;
        line-height: 50px;
    }
    .el-upload--text {
        color: #97a8be;
        font-size: 14px;
        text-align: center;
    }
    .el-upload--text em {
        font-style: normal;
    }
    .content-title {
        font-weight: 400;
        line-height: 50px;
        margin: 10px 0;
        font-size: 22px;
        color: #1f2f3d;
    }
    .pre-img {
        width:250px;
        height: 250px;
        margin-bottom: 20px;
    }
</style>



